import React from 'react';

const  Loader = () => {
	return (
		<div className='loader'>
			<div className="spinner">
				<div className="circle circle1"></div>
				<div className="circle circle2"></div>
				<div className="circle circle3"></div>
			</div>
		</div>
	);
}


export default Loader;



